.custom-select {
  position: relative;
  font-family: "Montserrat";
}
.custom-select select {
  display: none;
}
.custom-select .select-selected {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3px;
}
.custom-select .select-selected:hover {
  background: rgba(51, 125, 90, 0.1);
}
.custom-select .select-selected:after {
  font-family: icomoon;
  color: #93b474;
  font-size: 6px;
  content: "\e922";
  transform: rotate(180deg);
}
.custom-select .select-selected.select-arrow-active {
  border: 1.5px solid #337d5a;
}
.custom-select .select-selected.select-arrow-active:after {
  border-color: transparent transparent #93b474 transparent;
  transform: rotate(0deg);
}
.custom-select .select-items div,
.custom-select .select-selected {
  color: #1a2f3f;
  padding: 3px 0px 3px 5px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
}
.custom-select .select-items {
  position: absolute;
  background-color: #ffffff;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}
.custom-select .select-hide {
  display: none;
}
.custom-select .select-items .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}
.custom-select .select-items div:hover,
.custom-select .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}

.gallery__gallery-grid-blocks {
  display: grid;
  gap: 30px;
}
@media (max-width: 960px) {
  .gallery__gallery-grid-blocks {
    gap: 10px;
  }
}
.gallery__gallery-grid-blocks .grid-row-1,
.gallery__gallery-grid-blocks .grid-row-2,
.gallery__gallery-grid-blocks .grid-row-3 {
  display: grid;
  gap: 10px;
}
.gallery__gallery-grid-blocks .grid-row-1.reverse .item-gallery-grid-blocks:nth-child(2),
.gallery__gallery-grid-blocks .grid-row-2.reverse .item-gallery-grid-blocks:nth-child(2),
.gallery__gallery-grid-blocks .grid-row-3.reverse .item-gallery-grid-blocks:nth-child(2) {
  grid-column: 1/1;
}
.gallery__gallery-grid-blocks .grid-row-1.reverse .item-gallery-grid-blocks:nth-child(1),
.gallery__gallery-grid-blocks .grid-row-2.reverse .item-gallery-grid-blocks:nth-child(1),
.gallery__gallery-grid-blocks .grid-row-3.reverse .item-gallery-grid-blocks:nth-child(1) {
  grid-column: 2/2;
  grid-row: 1;
}
.gallery__gallery-grid-blocks .grid-row-1 {
  grid-template-columns: repeat(2, auto);
  justify-content: space-between;
}
.gallery__gallery-grid-blocks .grid-row-1 .item-gallery-grid-blocks:nth-child(1) {
  max-width: 739px;
  max-height: 556px;
}
@media (max-width: 960px) {
