.gallery__gallery-grid-blocks .grid-row-1 div,
.gallery__gallery-grid-blocks .grid-row-2 div,
.gallery__gallery-grid-blocks .grid-row-3 div {
  width: 100%;
  height: 100%;
}
.gallery__gallery-grid-blocks .item-gallery-grid-blocks {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
}
.gallery__gallery-grid-blocks .item-gallery-grid-blocks__picture-block {
  width: 100%;
  height: 100%;
}
.gallery__gallery-grid-blocks .item-gallery-grid-blocks__picture-block img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.gallery__gallery-grid-blocks .text-block,
.gallery__gallery-grid-blocks .video-block {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  font-family: "Montserrat";
  color: #ffffff;
}
.gallery__gallery-grid-blocks .text-block {
  background-color: rgba(51, 125, 90, 0.97);
  padding: 75px 48px 20px 39px;
}
.gallery__gallery-grid-blocks .text-block .text-block__title {
  font-size: 22px;
  font-weight: 800;
}
.gallery__gallery-grid-blocks .text-block .text-block__text {
  margin-top: 18px;
  font-size: 15px;
  font-weight: 500;
  max-width: 480px;
}
@media (max-width: 600px) {
  .gallery__gallery-grid-blocks .text-block {
    padding: 20px 20px 20px 20px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
.gallery__gallery-grid-blocks .video-block {
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.gallery__gallery-grid-blocks .video-block:hover {
  background: rgba(0, 0, 0, 0.55);
}
.gallery__gallery-grid-blocks .video-block .icon-play {
  position: relative;
  border: 2px solid #ffffff;
  border-radius: 50%;
  width: 61px;
  height: 61px;
}
@media (max-width: 600px) {
  .gallery__gallery-grid-blocks .video-block .icon-play {
    height: 45px;
    width: 45px;
  }
}
.gallery__gallery-grid-blocks .video-block .icon-play:before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-35%, -50%);
}
.gallery__gallery-grid-blocks .video-block.video-big {
